import {
	mapState,
	mapMutations,
} from 'vuex'
export default {
	components: {},
	computed: mapState(['valveNum']),
	data() {
		return {
			createDateF: undefined,
			entryTimeF: undefined,
			isUnbindCldVisible: false,
			unBindCldList: [{
				"data": []
			}],
			valveDetail: {},
			// 当前阀是否绑定
			isBind: false,
		}
	},
	methods: {
		...mapMutations(['valveDeleteSuccess']),
		transDate() {
			this.createDateF = this.parseTime(this.valveDetail.valveCreateDate)
			this.entryTimeF = this.parseTime(this.valveDetail.valveEntryTime)
		},
		bindDeleteValve() {
			uni.showModal({
				title: '删除气阀',
				content: '是否删除',
				success: (res) => {
					if (res.confirm) {
						uni.request({
							url: this.constVal.baseURL + '/system/valve/',
							data: {
								valveId: this.valveDetail.valveId,
								stampId: this.valveDetail.stampId,
							},
							method: 'DELETE',
							success: (res) => {
								this.valveDeleteSuccess();
							}
						})
					} else if (res.cancel) {
						return;
					}
				}
			})
		},
		// 显示未绑定Cld
		bindSortUnbindCld() {
			this.unBindCldList[0].data = [];
			this.getUnbindClds();
		},
		// 解绑
		unbindCV() {
			uni.showModal({
				title: '解绑',
				content: '是否解绑',
				success: (res) => {
					if (res.confirm) {
						uni.request({
							url: this.constVal.baseURL + '/system/valve/unbind',
							method: 'PUT',
							data: {
								stampId: this.valveDetail.stampId,
							},
							success: (res) => {
								if (res.data.code == 200) {
									this.isBind = false;
									this.getCurValve();
									uni.showToast({
										title: '解绑成功',
										image: '../../static/icons/check.png'
									})
								} else {
									uni.showToast({
										title: '解绑失败',
										image: '../../static/icons/close.png',
									})
								}
							}
						})
					} else if (res.cancel) {
						return;
					}
				}
			})
		},
		bindCldClick(e) {
			this.isUnbindCldVisible = false;
			uni.showModal({
				title: '绑定钢瓶',
				content: e.item.name,
				success: (res) => {
					if (res.confirm) {
						uni.request({
							url: this.constVal.baseURL + '/system/valve/bind',
							data: {
								stampId: e.item.name,
								valveNum: this.valveDetail.valveNum,
							},
							method: 'PUT',
							success: (res) => {
								uni.showToast({
									title: '绑定成功',
									image: '../../static/icons/check.png',
								})
								this.isBind = true;
								this.getCurValve();
							}
						})
					} else if (res.cancel) {
						return;
					}
				}
			})
		},
		getCurValve() {
			uni.request({
				url: this.constVal.baseURL + '/system/valve/list',
				data: {
					valveNum: this.valveNum
				},
				success: (res) => {
					this.valveDetail = res.data.rows[0];
					if (this.valveDetail.stampId != '') { //已绑定
						this.isBind = true;
					} else { //未绑定
						this.isBind = false;
					}
				}
			})
		},
		getUnbindClds() {
			uni.request({
				url: this.constVal.baseURL + '/system/cld/unbindList',
				success: (res) => {
					var Clds = res.data.rows;
					if (Clds == []) {
						uni.showToast({
							title: '当前无未绑定钢瓶',
							duration: 2000,
							image: '../../static/icons/forbidden.png',
						})
					} else {
						this.isUnbindCldVisible = true;
						Clds.forEach(item => {
							this.unBindCldList[0].data.push(item.stampId);
						})
					}
				}
			})
		},

	},
	onLoad() {
		this.transDate();
		this.getCurValve();
	}
}
